@import "_config.scss";

nav {
    box-shadow: 8px 8px 4px rgba(50, 50, 50, 0.7);
    
    #w-title {
        font-family: $titleFont;
        font-size: 25px;
        font-weight: bold;
        color: $aqua;
    }

    #w-hat {
        display: inline-block;
        width: 1.2em;
        height: 1.2em;
        background: url('../imgs/hat_mustache.jpg') no-repeat center;
        background-size: cover;
    }

}

#main-search {
    padding-top: 10em;
    background: url('../imgs/wooden_board.jpg') no-repeat center;
    background-size: cover;
    height: 80vh;

    h1 {
        text-align: center;
        margin-bottom: 0.5em;
        font-family: $titleFont;
        font-weight: bold;
    }

    input {
        margin-top: 1em;
        padding: 0.5em 1.5em 0.5em 1.5em;
        width: 23em;
        margin: 0 auto;
        border-radius: 35px;
        border: 0;
        box-shadow: 8px 8px 4px rgba(50, 50, 50, 0.7);
        font-family: $handWrittenFont;
        font-size: 1.4em;
        color: #666;
    }

    #search-btn {
        @include Wbuttons($aqua, $aquaHover);
        margin: 2em auto;        
    }
}

.spooner {
    display: block;
    text-align: center;
    margin: 0 auto;

    .loading {
        display: inline-block;
        animation: spin 2s linear infinite;
        @include spin;
    }
}

#custom-alert {
    position: absolute;
    background-color: #dedede;
    top: 60px;
    padding-top: 15px;
    width: 40em;
    text-align: center;
    border-radius: 5px 5px 35px 35px;
    border: 3px solid #444;
}

#search-results {
    margin-top:20em;

    ul {
        list-style: none;
    }

    .recipe-prev-card {
        display: flex;
        flex-flow: row wrap;
        width: 100%;
        box-shadow: 4px 4px 15px rgba(50, 50, 50, 0.3);
        min-height: 234px;
        margin-bottom: 2em;
    }

    .recipe-prev-img {
        width: 50%;
        max-height: 100%;

        div {
            width: 100%;
            height: 100%;
        }
    }

    .recipe-prev-text {
        text-align: center;
        margin: 1.5em auto;
        max-width: 50%;
        padding: 0.7em;
        
        .cook-btn {
            @include Wbuttons($aqua, $aquaHover);
            margin: 1em auto;
        }

        .save-btn {
            @include Wbuttons($pink, $pinkHover);
            margin: 1em auto;
            
            .fa-bookmark {
                margin-left: 6px;
            }
        }
    }

    .recipe-details {
        .modal-content {
            border: 0;
            border-radius: 0;
            background-color: $aqua;
            
            .modal-title {
                color: #FFF;
            }
        }

        button {
            @include Wbuttons(#FFF, #FFF);
            box-shadow: 0 0 0;
            color: $aqua;
            width: 6em;
            margin: 0 1em 1em auto;
            &:hover {
                color: $aquaHover;
            }
        }
    }
}

#signup-section {
    background: url('../imgs/register_bg.jpg') no-repeat center;
    background-size: cover;
    height: 100vh;
    .container {
        width: 50%;
        margin: 0 3em 0 auto; 
        padding-top: 5em;
        h1 {
            font-family: $handWrittenFont;
            color: #FFF;
            font-weight: bold;
            letter-spacing: 2px;
            text-shadow: 1px 1px 1px #444;
        }
        input {
            border-radius: 35px;
            border: 0;
            box-shadow: 4px 4px 2px rgba(50, 50, 50, 0.7);
            font-family: $handWrittenFont;
            font-size: 1.4em;
            color: #666;
            margin-top: 1.5em;
        }
        button {
            @include Wbuttons(#aa020d, #ff0110);
            margin: 2em auto;
        }
    }
}

#login-section {
    background: url('../imgs/login_bg.jpg') no-repeat center;
    background-size: cover;
    height: 100vh;
    .container {
        width: 50%;
        // margin: 0 3em 0 auto; 
        padding-top: 5em;
        input {
            border-radius: 35px;
            border: 0;
            box-shadow: 4px 4px 2px rgba(50, 50, 50, 0.7);
            font-family: $handWrittenFont;
            font-size: 1.4em;
            color: #666;
            margin-top: 1.5em;
        }

        h1 {
            font-family: $handWrittenFont;
            color: $aqua;
            font-weight: bold;
            letter-spacing: 2px;
            text-shadow: 1px 1px 1px #444;
        }
        button {
            @include Wbuttons($aqua, $aquaHover);
            margin: 2em auto;
        }

    }
}



